import React from 'react'
import { Link } from "gatsby"

export default ({ pageContext: {segments} }) => (
    <div style={{ width: 960, margin: "4rem auto" }}>
      {segments.map(segment => {
        return <p><Link to={`/Reader/${segment.language}/${segment.version}/${segment.book}/${segment.chapter}`}>{`/Reader/${segment.language}/${segment.version}/${segment.book}/${segment.chapter}`}</Link></p>
      })}
    </div>
  )